<template>
  <div class="body " :class="themeClass">
    <header class="container">
      <div class="img-fluid">
        <router-link :to="{name: 'HomeViews'}">
          <img src="@/assets/logo-white.png" alt="Logo" v-if="isDarkTheme">
          <img src="@/assets/logo-dark.png" alt="Logo" v-else>
        </router-link>
      </div>
      <div :class="{active: this.showMenu}" class="sidebar">
        <nav >
          <div class="genre " >
            <h2>Genres</h2>
            <ul>
              <li v-for="genre in genres" :key="genre.id" @click="getGenreList(genre.id)">
                {{ genre.name }}
              </li>
            </ul>
            <h2>Language</h2>
            <ul>
              <li v-for="language in languages" :key="language.iso_639_1" @click="getLanguageList(language.iso_639_1)">
                {{ language.english_name }}
              </li>
            </ul>
            <div class="icon-genre">
              <font-awesome-icon :icon="['fas', 'arrow-left']" @click="showMenuToggle"/>
            </div>
          </div>
        </nav>
        <div class="overlay" @click="showMenuToggle"></div>
      </div>
      <div class="option">
        
        <div class="icon" @click="closeSearch">
          <font-awesome-icon :icon="['fas', 'search']" />
        </div>
        <div class="icon ms-2">
          <font-awesome-icon :icon="['fas', 'user']" />
        </div>
        <div class="swiBtn ms-2" @click="toggleTheme">
          <div class="light icon" v-if="isDarkTheme">
            <font-awesome-icon :icon="['fas', 'sun']" />
          </div>
          <div class="dark icon" v-else>
            <font-awesome-icon :icon="['fas', 'moon']" />
          </div>
        </div>
        <div class="icon ms-2">
          <font-awesome-icon :icon="['fas', 'x']" @click="showMenuToggle" v-if="showMenu" />
          <font-awesome-icon :icon="['fas', 'bars']" @click="showMenuToggle" v-else />
        </div>
      </div>
    </header>
    <div class="pop" :class="{active : this.showSearch}" >
      <div class="form-group">
        <input type="text"  v-model="query" @input="debounceSearch" id="pop-search" placeholder="Search Movies">
        <button type="submit"   ><font-awesome-icon :icon="['fas', 'search']" /></button>
        <div class="icon-pop">
          <font-awesome-icon :icon="['fas', 'x']" @click="closeSearch"  />
        </div>
      </div>

      <div class="p-4 list-movies search"  :class="{active : this.moviesSearch.length}">
        <div class="row g-3 list-movie" >
          <div class="col-12"   v-for="movie in moviesSearch" :key="movie.id" @click="goToMovieDetail(movie.id)">
            <div class="card"  >
              <div class="img-fluid">
                <img :src="getBackdropUrl(movie.poster_path)" alt="Movie Poster">
              </div>
              <div class="card-body">
                <h5 class="card-title">{{ movie.title }}</h5>
                <p class="card-text">{{ movie.overview }}</p>
                <button class="btn btn-primary">View Details</button>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <div class="overlay" :class="{active : this.showSearch}" @click="closeSearch"></div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios';
import AOS from 'aos';
export default {
  name: 'App',
  data() {
    return {
      isDarkTheme: false,
      showSearch: false,
      showMenu: false,
      query:'',
      moviesSearch:[],
      genres:[],
      languages:[],
      selectedLanguages: ['ar', 'fr', 'en'],
    }
  },
  computed: {
    themeClass() {
      return this.isDarkTheme ? 'dark-theme' : 'light-theme';
    }
  },
  methods: {
    getGenreList(id){
      this.$router.push({ name: 'GenreMovie', params: { genreId: id} })
      this.showMenu = !this.showMenu;
    },
    getLanguageList(id) {
      this.$router.push({ name: 'LanguageMovie', params: { languageId: id } });
      this.showMenu = !this.showMenu; 
    },
    goToMovieDetail(id) {
      this.$router.push({ name: 'MovieDetail', params: { id } });
      this.showSearch = !this.showSearch;
      this.moviesSearch = [];
      this.query = '';
      this.$nextTick(() => {
        AOS.refresh();
      });
    },
    fetchGenres() {
      axios.get(`https://api.themoviedb.org/3/genre/movie/list?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
        .then(response => {
          this.genres = response.data.genres;
        })
        .catch(error => {
          console.error("There was an error fetching the genres!", error);
        });
    },
    fetchLanguages() {
      axios.get(`https://api.themoviedb.org/3/configuration/languages?api_key=7ff031c5231d770b6d6e548be216c062`)
        .then(response => {
          this.languages = response.data.filter(language => this.selectedLanguages.includes(language.iso_639_1));
        })
        .catch(error => {
          console.error("There was an error fetching the languages!", error);
        });
    },
    debounceSearch(){
      if (this.searchTimeout) {
        clearTimeout(this.searchTimeout);
      }
      
      this.searchTimeout = setTimeout(() => {
        this.searchMovies();
      }, 1000);
    },
    searchMovies() {
      if (this.query.trim() === '') return;

      axios.get(`https://api.themoviedb.org/3/search/movie?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US&query=${this.query}&page=1&include_adult=false`)
        .then(response => {
          this.moviesSearch = response.data.results.filter(movie => movie.poster_path);
        })
        .catch(error => {
          console.error("There was an error searching for the movies!", error);
        });
    },
    getBackdropUrl(path) {
            return `https://image.tmdb.org/t/p/w780${path}`; 
        },
    toggleTheme() {
      this.isDarkTheme = !this.isDarkTheme;
    },
    closeSearch(){
      this.showSearch = !this.showSearch;
      this.moviesSearch = [];
      this.query = '';
    },
    showMenuToggle(){
      this.showMenu = !this.showMenu;
    }
  },
  mounted() {
    this.fetchGenres();
    this.fetchLanguages();
    AOS.init();
  },
  updated() {
    this.$nextTick(() => {
      AOS.refresh();
    });
  },
};

</script>

