import { createRouter, createWebHistory } from 'vue-router'
import HomeViews from '../Views/HomeViews.vue'
import MovieDetail from '../Views/MovieDetail.vue'
import GenreMovie from '../Views/GenreMovie.vue'
import LanguageMovie from '../Views/LanguageMovie.vue'

const routes = [
    {
        path: '/mj-movie/',
        name: 'HomeViews',
        component: HomeViews
    },
    {
        path: '/mj-movie/MovieDetail/:id',
        name: 'MovieDetail',
        component: MovieDetail,
        props: true
    },
    {
        path: '/mj-movie/GenreMovie/:genreId',
        name: 'GenreMovie',
        component: GenreMovie,
        props: true
    },
    {
        path: '/mj-movie/LanguageMovie/:languageId',
        name: 'LanguageMovie',
        component: LanguageMovie,
        props: true
    },

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
