<template>
    <section class="movieDetail">
        <div class="container">
            <div v-if="movie">
                <div class="movie-details d-flex justify-content-start align-items-start">
                    <div class="img-fluid poster">
                        <img :src="getPosterUrl(movie.poster_path)" alt="Movie Poster">
                    </div>
                    <div class="movie-text text-start">
                        <p class="text-uppercase">
                            <span class=" text-capitalize">Name</span> :
                            {{ movie.title }}
                        </p>
                        <p v-if="Array.isArray(movie.genres) && movie.genres.length">
                            <span>Genre</span> :
                            {{ movie.genres.map(genre => genre.name).join(', ') }}
                        </p>
                        <p class="text-uppercase">
                            <span class="text-capitalize">Language</span> :
                            {{ movie.original_language}}
                        </p>
                        <p class="lin-3">
                            <span>Overview</span> :
                            {{ movie.overview }}
                        </p>
                        <div class="d-flex align-items-center meta-list">
                            <div class="meta-item"> 
                                <font-awesome-icon class="fonticon" :icon="['fas', 'calendar-days']" />
                                {{ getYear(movie.release_date) }}
                            </div>
                            <div class="meta-item">
                                <font-awesome-icon class="fonticon" :icon="['fas', 'percent']" />
                                {{ getFormattedRating(movie.vote_average) }}
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
            <div v-else>
                <p>No movie found</p>
            </div>
        </div>
    </section>
    
    <section class="section">
        <div class="container section-title text-start">
            <h1>Similar Movies</h1>
        </div>
        <div class="container list-movies">
            <div class="row g-4 list-movie" v-if="similarMovies.length">
                <div class="col-lg-4 col-md-6 col-sm-12" v-for="moviE in similarMovies" :key="moviE.id" @click="goToMovieDetail(moviE.id)">
                    
                    <div class="card">
                        <div class="img-fluid">
                            <img :src="getPosterUrl(moviE.poster_path)" alt="Movie Poster">
                        </div>
                        <div class="card-body">
                            <h5 class="card-title">{{ moviE.title }}</h5>
                            <div class="meta-list">
                                <div class="meta-item rate">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'percent']" />
                                    {{ getFormattedRating(moviE.vote_average) }}
                                </div>
                                <div class="meta-item time">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'calendar-days']" />
                                    {{ getYear(moviE.release_date) }}
                                </div>
                            </div>
                            <div class="genre">
                                <p>
                                    {{ getGenreNames(moviE.genre_ids).join(', ') }}
                                </p>
                            </div>
                            <p class="card-text">{{ moviE.overview }}</p>
                            <button class="btn btn-primary">View Details</button>
                        </div>
                    </div>
                </div>
            </div>
            <p v-else>No similar movies found.</p>
        </div>
    </section>
</template>

<script>
import axios from 'axios';

export default {
    props: ['id'],
    name:'MovieDetail',
    data() {
        return {
            movie: {},
            
            genres: [],
            trailerUrls: [],
            similarMovies: [],
        };
    },
    methods: {
        goToMovieDetail(id) {
            this.$router.push({ name: 'MovieDetail', params: { id } });
        },
        fetchGenres() {
            axios.get(`https://api.themoviedb.org/3/genre/movie/list?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
            .then(response => {
                this.genres = response.data.genres;
                console.log(this.movie)
            })
            .catch(error => {
                console.error("There was an error fetching the genres!", error);
            });
        },
        getGenreName(genreIds) {
            if (!this.genres.length) return [];
            return genreIds
            .map(id => {
                const genre = this.genres.find(g => g.genres === id);
                return genre ? genre.name : null;
            })
            .filter(name => name);
        },
        getGenreNames(genreIds) {
            if (!this.genres.length) return [];
            return genreIds
            .map(id => {
                const genre = this.genres.find(g => g.id === id);
                return genre ? genre.name : null;
            })
            .filter(name => name);
        },
        getPosterUrl(path) {
            return `https://image.tmdb.org/t/p/w500${path}`;
        },
        getYear(date) {
            return new Date(date).getFullYear();
        },
        getFormattedRating(rating) {
        return rating ? rating.toFixed(1) : 'N/A';
        },
        fetchMovieTrailer() {
            axios.get(`https://api.themoviedb.org/3/movie/${this.id}/videos?api_key=7ff031c5231d770b6d6e548be216c062`)
            .then(response => {

                let trailer = response.data.results;

                this.trailerUrls = `https://www.youtube.com/watch?v=${trailer.key}`;
                

            })
            .catch(error => {
                if (error.response && error.response.status === 404) {
                    console.error("Movie not found (404). Please check the movie ID or availability of trailers.");
                } else {
                    console.error("There was an error fetching the movie trailer!", error);
                }
            });
        },
        fetchMovie() {
            axios.get(`https://api.themoviedb.org/3/movie/${this.id}?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
            .then(response => {
                this.movie = response.data;
                this.fetchGenres();
                this.getGernes();
                console.log(this.trailerUrls);
                console.log(this.id);
            })
            .catch(error => {
                console.error("There was an error fetching the movie details!", error);
            });
        },
        fetchSimilarMovies() {
            axios.get(`https://api.themoviedb.org/3/movie/${this.id}/similar?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
            .then(response => {
            this.similarMovies = response.data.results.slice(0, 6);
            })
            .catch(error => {
            console.error("There was an error fetching similar movies!", error);
            });
        },
    },
    mounted() {
        this.fetchMovie();
        this.fetchSimilarMovies();
        this.fetchMovieTrailer();
    },
    watch: {
        id(newId, oldId) {
            if (newId !== oldId) {
                this.fetchMovie();
                this.fetchSimilarMovies();
                this.fetchMovieTrailer();
            }
        }
    }
};
</script>


<style lang="scss">
.movie-details{
    height:550px;
    overflow:hidden;
    margin-block-start: 20px;
    position: relative;
    border:1px solid var(--dark-light);
    border-radius: 10px;
    padding:35px 0;
    background-image: linear-gradient(90deg , var(--primary-color) -150%, #eeeeee00);
    @media screen and (max-width: 768px) {
        height:300px;
    }
    .poster{
        width: 40% !important;
        img{
            border-radius: 10px;
            width: 86%;
        }
    }
    .movie-text{
        width: 60% !important;
        margin-block: auto;
        p{
            
            font-size: 1.5rem;
            font-weight: 400;
            margin-block-start: 0.8rem;
            span{
                color: var(--primary-color);
                font-size: 1.7rem;
                font-weight: 600;
            }
            &.lin-3{
                display: -webkit-box;       
                -webkit-box-orient: vertical;
                overflow: hidden;            
                text-overflow: ellipsis;     
                -webkit-line-clamp: 6;             
                max-height: calc(1.5em * 6);
                line-height: 1.5em;    
                font-size:1.2rem;
                color: var(--dark-color);
            }
            
        }
        .meta-list{
            margin-block-start: 1.2rem;
            .meta-item{
                font-size: 1.2rem;
                margin-inline-end: 10px;
                background-color: var(--dark-color);
                color: var(--light-color);
                padding: 5px 10px;
                border-radius: 5px;
                .fonticon{
                    color:var(--success-color)
                }
            }
        }
    }
}
.movie-overview{
    background-color: var(--dark-color);
    color: var(--light-color);
    border-radius: 10px;
    margin-block: 30px;
    padding: 15px 20px;
    h2{
        font-size: 2.5rem;
        font-weight: 700;
    }
    p{
        font-size: 1.2rem;
        font-weight: 400;
        margin-block-start: 0.8rem;
    }
}
@media (min-width: 1439px){
    .movie-details .poster {
        width: 30% !important;
        img{
            width: 82% !important;
        }
    }
}
@media (max-width: 769px){
    .movie-details {
        padding: 15px 0;
        height: 100%;
        .poster {
            width: 50% !important;
            img{
                width: 86% !important;
            }
        }
        .movie-text {
            width: 65% !important;
            margin-block: auto;
            p{
                font-size: 1.2rem;
                margin-block-start: 0 !important;
                margin-bottom: 15px !important;

                &.lin-3{
                    font-size: 1.2rem;
                    -webkit-line-clamp: 4;             
                    max-height: calc(1.5em * 4);
                }
                span{
                    font-size: 1.4rem;
                }
            }
            .meta-list{
                .meta-item {
                    font-size: 0.8rem;
                    margin-inline-end: 30px;
                    padding: 5px 5px;
                    
                }
            }
        }
    }
}
@media (max-width: 426px){
    
    .movie-details{
        flex-direction: column;
        height: 100%;
        padding: 20px 15px;
        .poster {
            width: 75% !important;
            margin-inline: auto;
            margin-block-end: 20px;
            img{
                width: 100% !important;
            }
        }
        .movie-text {
            width: 100% !important;
            margin-block: auto;
            .meta-list {
                margin-block-start: 2.2rem;
                justify-content: end;
                .meta-item{
                    font-size: 1rem;
                    margin-inline-end: 30px;
                    padding: 6px 10px;
                }
            }
        }
    }
}
</style>