<template>
    <section class="section slider">
        <div class="slider-wrapper">
            <div class="slider-content" :style="sliderStyle">
            <div class="slide" v-for="(movie, index) in sliderMovies" :key="movie.id" :class="{ active: index === currentIndex }">
                <div class="img-fluid">
                    <img :src="getBackdropUrl(movie.backdrop_path)" alt="Movie Backdrop">
                </div>
                <div class="slide-caption">
                    <div class="text">
                        <h3>{{ movie.title }}</h3>
                        <p class="genre">{{ getGenreNames(movie.genre_ids).join(', ') }}</p>
                        <p>{{ movie.overview }}</p>
                        <button class="btn btn-primary" @click="goToMovieDetail(movie.id)">View Details</button>
                    </div>
                </div>
            </div>
            </div>
            <div class="slider-controls">
            <button @click="prevSlide" class="prev-btn"><font-awesome-icon :icon="['fas', 'arrow-left']" /></button>
            <button @click="nextSlide" class="next-btn"><font-awesome-icon :icon="['fas', 'arrow-right']" /></button>
            </div>
        </div>
    </section>

    <section class="section">
        <div class="container section-title text-start">
            <h1>What's Popular</h1>
        </div>
        <div class="container list-movies">
            <div class="row g-4 list-movie" v-if="movies.length">
                <div class="col-lg-4 col-md-6 col-sm-12" v-for="movie in movies" :key="movie.id" @click="goToMovieDetail(movie.id)">
                    
                    <div class="card">
                        <div class="img-fluid">
                            <img :src="getImageUrl(movie.poster_path)" alt="Movie Poster">
                        </div>
                        <div class="card-body">
                            <h5 class="card-title">{{ movie.title }}</h5>
                            <div class="meta-list">
                                <div class="meta-item rate">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'percent']" />
                                    {{ getFormattedRating(movie.vote_average) }}
                                </div>
                                <div class="meta-item time">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'calendar-days']" />
                                    {{ getYear(movie.release_date) }}
                                </div>
                            </div>
                            <div class="genre">
                                <p>
                                    {{ getGenreNames(movie.genre_ids).join(', ') }}
                                </p>
                            </div>
                            <p class="card-text">{{ movie.overview }}</p>
                            <button class="btn btn-primary">View Details</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import axios from 'axios';

export default {
    name: 'HomeViews',
    data() {
        return {
            sliderMovies:[],
            movies:[],
            genres:[],
            currentIndex: 0,
        }
    },
    methods: {
        goToMovieDetail(id) {
            this.$router.push({ name: 'MovieDetail', params: { id } });
        },
        prevSlide() {
        this.currentIndex = (this.currentIndex - 1 + this.sliderMovies.length) % this.sliderMovies.length;
        },
        nextSlide() {
        this.currentIndex = (this.currentIndex + 1) % this.sliderMovies.length;
        },
        startAutoSlide() {
            this.autoSlideInterval = setInterval(() => {
            this.nextSlide();
        }, 5000);
        },
        stopAutoSlide() {
            clearInterval(this.autoSlideInterval);
        },
        fetchGenres() {
            axios.get(`https://api.themoviedb.org/3/genre/movie/list?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
            .then(response => {
                this.genres = response.data.genres;
            })
            .catch(error => {
                console.error("There was an error fetching the genres!", error);
            });
        },
        fetchMovies() {
            axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US&page=1`)
            .then(response => {
                this.movies = response.data.results;
            })
            .catch(error => {
                console.error("There was an error fetching the movies!", error);
            });
        },
        fetchMoviesSlider() {
        axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US&page=1`)            .then(response => {
            this.sliderMovies = response.data.results.filter(movie => movie.backdrop_path).slice(6, 15);
            })
            .catch(error => {
            console.error("There was an error fetching the movies!", error);
            });
        },
        getImageUrl(path) {
            return `https://image.tmdb.org/t/p/w500${path}`;
        },
        getYear(date) {
            return new Date(date).getFullYear();
        },
        getFormattedRating(rating) {
            return rating.toFixed(1); 
        },
        getBackdropUrl(path) {
            return `https://image.tmdb.org/t/p/w780${path}`; 
        },
        getGenreNames(genreIds) {
            if (!this.genres.length) return [];
            return genreIds
            .map(id => {
                const genre = this.genres.find(g => g.id === id);
                return genre ? genre.name : null;
            })
            .filter(name => name);
        },
    },
    mounted() {
        this.fetchMoviesSlider();
        this.fetchMovies();
        this.fetchGenres();
        this.startAutoSlide();
    },
    computed: {
        sliderStyle() {
            return {
                transform: `translateX(-${this.currentIndex * 100}%)`,
            };
        },
    },
}
</script>

<style lang="scss" >
.slider{
    width: 100%;
    overflow: hidden;
    position: relative;
    .slider-wrapper{
        position: relative;
        width: 100%;
        overflow: hidden;
        .slider-content{
            width: 100%;
            height: 500px;
            display: flex;
            transition: transform 0.5s ease-in-out;
            .slide{
                min-width: 100%;
                height: 100%;
                position: relative;
                opacity: .5;
                transition: opacity 0.5s ease-in-out;
                &.active{
                    opacity: 1;
                }
                img{
                    width:100%;
                    height: auto;
                    object-fit: cover;
                }
                .slide-caption{
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    width:100%;
                    height: 100%;
                    background-color: rgb(52 58 64 / 29%);
                    padding: 10px;
                    color: #F8F9FA;
                    .text{
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 50px;
                        width: 50%;
                        text-align: start;
                        h3{
                            font-size: 2.5rem;
                            font-weight: 700;
                            margin-block-end: 1.5rem;
                        }
                        p{
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 2;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            font-size: 1.5rem;
                        }
                        .genre{
                            margin-block: -18px 10px;
                            font-size: 1.2rem;
                        }
                        .btn-primary{
                            background-color: transparent;
                            border: 2px solid var(--light-color) !important;
                            color: var(--light-color);
                            font-weight: bold;
                            border: none;
                            width:50%;
                            border-radius: 10px;
                            padding: 10px 10px;
                            margin-block-start: 10px;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
        .slider-controls{
            position: absolute;
            bottom: 60px;
            display: flex;
            align-items: center;
            width:100%;
            button{
                position: absolute;
                background: none;
                border: none;
                cursor: pointer;
                outline: none;
                font-size: 1.5rem;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                display: grid;
                place-items: center;
                background-color: var(--light-color);
                border: 1px solid var(--dark-color);
                color: var(--dark-color);
                transition: all 0.3s ease-in-out;
                &:hover{
                    background-color: var(--dark-color);
                    color: var(--light-color);
                }
                &.prev-btn{
                    right: 8%;
                }
                &.next-btn{
                    right: 2%;
                }
            }

        }
    }
}
@media (max-width: 770px) {
    .slider .slider-wrapper .slider-content .slide img{
        width: 120% !important;
    }
}
@media (max-width: 426px) {
    .slider .slider-wrapper .slider-content .slide .slide-caption .text{
        width:90%;
        left: 18px;
        h3{
            font-size: 1.5rem !important;
        }
        p{
            font-size: 1.2rem !important;
        }
        .genre{
            font-size: 1rem !important;
        }
        button{
            font-size: 1.2rem !important;
            width: 50% !important;
        }
    }
    .slider{
        height: 400px !important;
        .slider-wrapper{
            height: 100%;
            .slider-content{
                height: 100%;
            }
        }
    }
    .slider .slider-wrapper .slider-content .slide img{
        width: 170% !important;
    }
    .slider .slider-wrapper .slider-controls button{
        font-size: 1rem !important;
        width: 25px !important;
        height: 25px !important;
    }
    .slider .slider-wrapper .slider-controls button.prev-btn {
    right: 10%;
}
}
@media (max-width: 376px) {
    .slider {
        height: 360px !important;
    }
}
@media (max-width: 321px) {
    .slider {
        height: 305px !important;
    }
}
@media (min-width: 2555px) {
    .slider {
        height: 900px;
        .slider-wrapper {
            height: 100%;
            .slider-content{
                height: 100%;
            }
            .slider-controls{
                
                button {
                    font-size: 2.5rem !important;
                    width: 80px !important;
                    height: 80px !important;
                    &.prev-btn {
                        right: 7% !important;
                    }
                }
            }
        }
    }
}
</style>