<template>
    <section class="section">
        <div class="container section-title text-start">
            <h1>Movies in {{ languageName }} Language</h1>
        </div>
        <div class="container list-movies">
            <div class="row g-4 list-movie" v-if="movies.length">
                <div class="col-lg-4 col-md-6 col-sm-12" v-for="movie in movies" :key="movie.id" @click="goToMovieDetail(movie.id)">
                    
                    <div class="card">
                        <div class="img-fluid">
                            <img :src="getImageUrl(movie.poster_path)" alt="Movie Poster">
                        </div>
                        <div class="card-body">
                            <h5 class="card-title">{{ movie.title }}</h5>
                            <div class="meta-list">
                                <div class="meta-item rate">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'percent']" />
                                    {{ getFormattedRating(movie.vote_average) }}
                                </div>
                                <div class="meta-item time">
                                    <font-awesome-icon class="fonticon" :icon="['fas', 'calendar-days']" />
                                    {{ getYear(movie.release_date) }}
                                </div>
                            </div>
                            <div class="genre">
                                <p>
                                    {{ getGenreNames(movie.genre_ids).join(', ') }}
                                </p>
                            </div>
                            <p class="card-text">{{ movie.overview }}</p>
                            <button class="btn btn-primary">View Details</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import axios from 'axios';

export default {
    name:'LanguageMovie',
    props: ['languageId'],
    data() {
        return {
            selectedLanguages: ['ar', 'fr', 'en'],
            movies: [],
            genres: [],
            languages: [],
            languageName: ''
        }
    },
    methods: {
        fetchMoviesByLanguage() {
            axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=7ff031c5231d770b6d6e548be216c062&language=${this.languageId}`)
            .then(response => {
            this.movies = response.data.results.filter(movie => movie.overview && movie.overview.trim() !== '');
            })
            .catch(error => {
            console.error("There was an error fetching movies by language!", error);
            });
        },
        fetchLanguages() {
        axios.get(`https://api.themoviedb.org/3/configuration/languages?api_key=7ff031c5231d770b6d6e548be216c062`)
            .then(response => {
            this.languages = response.data.filter(language => this.selectedLanguages.includes(language.iso_639_1));
            this.setLanguageName();
            this.fetchMoviesByLanguage();
            this.fetchGenres();
            })
            .catch(error => {
            console.error("There was an error fetching the languages!", error);
            });
        },
        setLanguageName() {
            if (Array.isArray(this.languages)) {
                const language = this.languages.find(g => g.iso_639_1 === this.languageId);
                if (language) {
                this.languageName = language.english_name;
                } else {
                this.languageName = 'Unknown '; 
                }
            } else {
                console.error("Languages is not an array!");
            }
        },
        getImageUrl(path) {
        return `https://image.tmdb.org/t/p/w500${path}`;
        },
        getYear(date) {
            return new Date(date).getFullYear();
        },
        getFormattedRating(rating) {
            return rating.toFixed(1); 
        },
        getBackdropUrl(path) {
            return `https://image.tmdb.org/t/p/w780${path}`; 
        },
        fetchGenres() {
            axios.get(`https://api.themoviedb.org/3/genre/movie/list?api_key=7ff031c5231d770b6d6e548be216c062&language=en-US`)
            .then(response => {
                this.genres = response.data.genres;
            })
            .catch(error => {
                console.error("There was an error fetching the genres!", error);
            });
        },
        getGenreNames(genreIds) {
            if (!this.genres.length) return [];
            return genreIds
            .map(id => {
                const genre = this.genres.find(g => g.id === id);
                return genre ? genre.name : null;
            })
            .filter(name => name);
        },
        goToMovieDetail(id) {
            this.$router.push({ name: 'MovieDetail', params: { id } });
        },
    },
    mounted(){
        this.fetchLanguages();
    },
    watch:{
        languageId(newId, oldId) {
            if (newId !== oldId) {
                this.fetchLanguages();
            }
        }
    }
}
</script>

